        // 分页
        contentPageStyleChange() {
            let arr = []
            if (this.contents.pageTotal) arr.push('total')
            if (this.contents.pageSizes) arr.push('sizes')
            if (this.contents.pagePrevNext) {
                arr.push('prev')
                if (this.contents.pagePager) arr.push('pager')
                arr.push('next')
            }
            if (this.contents.pageJumper) arr.push('jumper')
            this.layouts = arr.join()
            this.contents.pageEachNum = 10
        },

        init() {
        },
        search() {
            this.pageIndex = 1;
            this.getDataList();
        },
        // 获取数据列表
        getDataList() {
            this.dataListLoading = true;
            let params = {
                page: this.pageIndex,
                limit: this.pageSize,
                sort: 'id',
            }

             
            if (this.searchForm.newsName!= '' && this.searchForm.newsName!= undefined) {
                params['newsName'] = '%' + this.searchForm.newsName + '%'
            }
     
            if (this.searchForm.newsTypes!= '' && this.searchForm.newsTypes!= undefined) {
                params['newsTypes'] = this.searchForm.newsTypes
            }
                                    
            params['newsDelete'] = 1// 逻辑删除字段 1 未删除 2 删除


            this.$http({
                url: "news/page",
                method: "get",
                params: params
            }).then(({data}) => {
                if(data && data.code === 0){
                    this.dataList = data.data.list;
                    this.totalPage = data.data.total;
                }else{
                    this.dataList = [];
                    this.totalPage = 0;
                }
                this.dataListLoading = false;
            });

            //查询级联表搜索条件所有列表
            //查询当前表搜索条件所有列表
            //填充下拉框选项
            this.$http({
                url: "dictionary/page?dicCode=news_types&page=1&limit=100",
                method: "get",
            }).then(({data}) => {
                if(data && data.code === 0){
                    this.newsTypesSelectSearch = data.data.list;
                }
            });
        },
        //每页数
        sizeChangeHandle(val) {
            this.pageSize = val;
            this.pageIndex = 1;
            this.getDataList();
        },
        // 当前页
        currentChangeHandle(val) {
            this.pageIndex = val;
            this.getDataList();
        },
        // 多选
        selectionChangeHandler(val) {
            this.dataListSelections = val;
        },
        // 添加/修改
        addOrUpdateHandler(id, type) {
            this.showFlag = false;
            this.addOrUpdateFlag = true;
            this.crossAddOrUpdateFlag = false;
            if (type != 'info') {
                type = 'else';
            }
            this.$nextTick(() => {
                this.$refs.addOrUpdate.init(id, type);
            });
        },
        // 下载
        download(file) {
            window.open(" ${file} ")
        },
        // 删除
        deleteHandler(id) {
            var ids = id ? [Number(id)] : this.dataListSelections.map(item => {
                return Number(item.id);
            });

            this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning"
            }).then(() => {
                this.$http({
                    url: "news/delete",
                    method: "post",
                    data: ids
                }).then(({data}) => {
                    if(data && data.code === 0){
                        this.$message({
                            message: "操作成功",
                            type: "success",
                            duration: 1500,
                            onClose: () => {
                                this.search();
                            }
                        });
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            });
        },
        // 导入功能上传文件成功后调用导入方法
        newsUploadSuccess(data){
            let _this = this;
            _this.$http({
                url: "news/batchInsert?fileName=" + data.file,
                method: "get"
            }).then(({data}) => {
                if(data && data.code === 0){
                    _this.$message({
                        message: "导入公告信息数据成功",
                        type: "success",
                        duration: 1500,
                        onClose: () => {
                            _this.search();
                        }
                    });
                }else{
                    _this.$message.error(data.msg);
                }
            });

        },
        // 导入功能上传文件失败后调用导入方法
        newsUploadError(data){
            this.$message.error('上传失败');
        },
    }
};
